.stepper-small{
    width: 56px;
    height: 32px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 8px;
}
.stepper-small__box{
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#ECEBED;
}
